@use "./variables/link-hover-decoration" as *;
@use "./variables/code-color" as *;
@use "./variables/dark/code-color-dark" as *;
@use "./variables/mark-color" as *;
@use "./variables/dark/mark-color-dark" as *;
@use "./variables/mark-bg" as *;
@use "./variables/dark/mark-bg-dark" as *;
